<template>
  <v-container class="bg-white">
    <v-banner><h2 class="h4"><span class="cblu bbred">All Jobs</span></h2></v-banner>
    <v-row class="py-0 py-md-5">
      <v-col cols="12" md="3">
          <div class="mobile">
            <div class="d-flex justify-content-between">
              <v-btn small @click="price">Price</v-btn>
              <v-btn small @click="deals">Jobs Near By</v-btn>
            </div>
          </div>
          <div class="catside shadow desktop">
            <div class="h5 sec1">Price</div>
            <div class="range-slider sec1 bg-white p-2">
              <span class="d-flex">
                <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
              </span>
              <v-range-slider v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
            </div>
            <div class="h5 sec1">Jobs Near By</div>
            <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
              <div class="text-center small">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
            <v-btn color="success" class="my-2">Go</v-btn>
          </div>
          <v-dialog v-model="dialog1" persistent>
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Price</v-card-title>
                  <v-icon @click="dialog1 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text>
                <v-range-slider
                    v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
                <span class="d-flex">
                      <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                      <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
                    </span>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialog2" persistent>
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Deals Radar</v-card-title>
                  <v-icon @click="dialog2 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text>
                <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                <div class="text-center h6 link-dark">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialog3" persistent class="p-2">
            <v-card>
              <span class="d-flex justify-content-between">
                  <v-card-title>Change Category</v-card-title>
                  <v-icon @click="dialog3 = false" class="me-3">mdi-close</v-icon>
              </span>
              <hr class="m-0">
              <v-card-text class="pb-0">
                <v-autocomplete solo dense :items="cats" label="Category" class="mt-3" placeholder="Category"></v-autocomplete>
              </v-card-text>
              <div class="text-center">
                <b-button class="mb-3" variant="success" type="submit">Go</b-button>
              </div>
            </v-card>
          </v-dialog>
      </v-col>
      <v-col md="9">
        <v-row class="desktop" v-if="rlists.length > 1">
          <v-col cols="12" md="6" v-for="(job,i) in rlists" :key="i" :perPage="perPage">
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col cols="4">
                    <router-link to="/jobs/detail">
                          <div v-if="job.user_avatar==null">
                              <img :src="('https://placehold.co/600x600?text='+job.job_title)" :alt=job.job class="rounded-circle img-fluid">
                          </div>
                          <div v-else>
                              <img :src=(url+job.user_avatar) :alt=job.user_avatar class="rounded-circle img-fluid">
                          </div>
                    </router-link>
                  </v-col>
                  <v-col cols="8">
                      <router-link to="/jobs/detail">
                          <h5 class="link-dark fw-bold mb-0">Job: {{job.job_title}}</h5>
                          <h6 class="cblu fw-bold mb-0">{{job.name}}</h6>
                      </router-link>
                      <div class="fs-6 mb-0">Add to Wishlist<v-icon small>mdi-heart-outline</v-icon></div>
                      <div class="fs-6"><v-icon small>mdi-map-marker</v-icon>{{job.city}}, {{job.state}}</div>

                      <div class="fs-6"><span class="fw-bold link-dark fs-6">Min Experience: </span>{{job.min_exp}} + </div>
                      <div class="fw-bold link-dark fs-6">
                          ₹ {{job.salary_from}} <span class="fw-semibold">a Month</span>
                      </div>
                    <router-link to="/jobs/detail" class="mt-2 cred fs-6">Read More</router-link>
                  </v-col>
                </v-row>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
        <v-pagination v-if="rlists.length > 1" class="pagination pt-3" v-model="page" :length="Math.ceil(allJobs.length/perPage)" circle></v-pagination>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';
import { EventBus } from "@/event-bus";
export default {
  name: "JobsearchPage",
  components: {},
  data (){
    return{
          url:'https://bizlx.itechvision.in',
          page:1,
          perPage:10,
          allJobs:[],

          min_exp: [],
          salary_from: [],
          filterOption:[],
          dialog1: false,
          dialog2: false,
          dialog3: false,
          catvalue:null,
          selected: null,
          min: 0,
          max: 120000,
          range: [],
          range1: [0, 100],
          min1: 0,
          max1: 100,
      };
  },
  mounted(){
      // this.jobTypes();
      EventBus.$on('event_bus_emitted', (data) =>{
          this.allJobs = data.allJobs;
      });
      const config = {
          headers: { 'content-type': 'multipart/form-data' }
      }
      this.searchedCatData = JSON.parse(localStorage.getItem("catSearchedValues"));
      axios.post("search/category", this.searchedCatData, config)
          .then((resp) => {
            if(resp.data.dealsType=="jobs"){ // deals page
                this.allJobs = resp.data.allJobs;
                this.filterOption = resp.data.allJobs;

                    for(var j = 0; j < this.allJobs.length; j++) { // min exp
                      this.min_exp.push(this.allJobs[j].min_exp);
                    }
                    this.min_exp = [...new Set(this.min_exp.sort())];
                    console.warn(this.min_exp); 
  
                    for(var k = 0; k < this.allJobs.length; k++) { // salary
                      this.salary_from.push(this.allJobs[k].salary_from);
                    }
                    this.salary_from = [...new Set(this.salary_from.sort())];
                    this.range = [this.salary_from.slice(0)[0], this.salary_from.slice(-1)[0]]; // min & max salary set
            }else{
              this.$router.push({name: 'home'});
            }
          },
      );
  },
  methods: {
    price() {
      this.dialog1 = true;
    },
    deals() {
      this.dialog2 = true;
    },
    category() {
      this.dialog3 = true;
    },
  },
  computed:{
    rlists(){
      return this.allJobs.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    }
  },
}
</script>

<style scoped>
@media(max-width: 576px){
.v-card__subtitle, .v-card__text, .v-card__title {
    padding: 8px !important;
}
.v-card__subtitle, .v-card__text {
    line-height: 1.1rem;
}
}
.jobside{
background: #e5e5e5;
padding: 10px;
}

.catside{
  background: #e5e5e5;
  padding: 10px;
}
.sec1{
  background: #f2f2f2;
  padding: 5px 15px;
  margin-bottom: 20px;
}

</style>